ks-product-simple {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    width: 100%;
    text-align: center;

    color: var(--card-text-color);
}

@media (min-width: 360px) {
    ks-product-simple { width: calc(50% - 15px); }
}

@media (min-width: 640px) {
    ks-product-simple { width: 228px; }
}

ks-product-simple .top {
    display: block;
    padding: 15px;

    min-height: 200px;

    color: inherit !important;
    text-decoration: none !important;
    font-size: 14px;
}

ks-product-simple ks-img2 {
    height: auto;
    margin-bottom: 10px;
    max-width: 100%;
}

ks-product-simple .price > * {
    display: block;
    font-family: var(--font-emphasis);
}

ks-product-simple .price .previous {
    color: #888888;
    font-size: 15px;
}

ks-product-simple .price .current {
    color: var(--color-secondary);
    font-weight: bold;
    font-size: 17px;
}

ks-product-simple[unavailable] .top,
ks-product-simple[unavailable] .price {
    opacity: 0.6;
}

ks-product-simple[unavailable] .price .current {
    color: #252525;
}

@media (max-width: 420px) {
    ks-product-simple .top {
        font-size: 13px;
        padding: 8px;
    }

    ks-product-simple .price {
        line-height: 18px;
    }
}