import { Component, h, Prop, Element } from '@stencil/core';
import priceFormat from '../../global/functions/price';

@Component({
  tag: 'ks-product-simple',
  styleUrl: 'product-simple.css'
})
export class ProductSimple {

  @Prop() name: string;
  @Prop() img: string;
  @Prop() link: string;
  @Prop() currentPrice: number;
  @Prop() previousPrice: number;

  @Element() root: HTMLElement;

  render() {
    const currentPrice = priceFormat(this.currentPrice);
    const previousPrice = priceFormat(this.previousPrice);

    return [
        <a href={this.link} aria-label={this.name} class="top">
            <ks-img2 fill limit center src={this.img} width={280} height={280} alt={this.name} />
            <span>{this.name}</span>
        </a>,

        <div class="price">
            {this.previousPrice ?
                <s class="previous">{previousPrice}</s>
            : null}
            <span class="current">{currentPrice}</span>
        </div>
    ];
  }
}
